import React from 'react';

// Style sheets
import Style from './TempForecast.module.scss';

export function TempForecast(props){
    const location = props.location;
    let data = props.data;
    let numbers = []
    
    // This method wil calculate the time every 3 hrs from the current time multiplied by 3 
    function calcTime(timezone, isTime){
        const date = new Date();
        const time = date.setSeconds(timezone)
        const locationDate = new Date(time);

        const dateObject = {
            day: locationDate.getUTCDay(),
            date: "",
            hour: locationDate.getUTCHours(),
            minute: locationDate.getUTCMinutes(),
        } 
        
        if(dateObject.minute >= 0 && dateObject.minute < 10){
            dateObject.minute = `0${dateObject.minute}`
        }

        if(dateObject.hour >= 0 && dateObject.hour < 10){
            dateObject.hour = `0${dateObject.hour}`
        }

        switch (dateObject.day) {
            case 0:
                dateObject.date = "Sunday";
                break;
            case 1:
                dateObject.date = "Monday";
                break;
            case 2:
                dateObject.date = "Tuesday";
                break;
            case 3:
                dateObject.date = "Wednesday";
                break;
            case 4:
                dateObject.date = "Thursday";
                break;
            case 5:
                dateObject.date = "Friday";
                break;
            case 6:
                dateObject.date = "Saturday";
                break;
            default:
              return;
          }
        
        if(isTime === null){
            return `${dateObject.date}, ${dateObject.hour}:${dateObject.minute}`
        }else{
            let result = 0;

            switch(numbers.length){
                case 0:
                    result = parseInt(dateObject.hour) + 3
                    numbers = [6, 9, 12, 15, 18, 21, 0]
                    break;
                case 7:
                    result = parseInt(dateObject.hour) + 6;
                    numbers = [9, 12, 15, 18, 21, 0]
                    break;
                case 6:
                    result = parseInt(dateObject.hour) + 9;
                    numbers = [12, 15, 18, 21, 0]
                    break;
                case 5:
                    result = parseInt(dateObject.hour) + 12;
                    numbers = [15, 18, 21, 0]
                    break;
                case 4:
                    result = parseInt(dateObject.hour) + 15;
                    numbers = [18, 21, 0]
                    break;
                case 3:
                    result = parseInt(dateObject.hour) + 18;
                    numbers = [21, 0]
                    break;
                case 2:
                    result = parseInt(dateObject.hour) + 21;
                    numbers = [0]
                    break;
                case 1:
                    result = parseInt(dateObject.hour) + 0;
                    numbers = []
                    break;
                default:
                    break;
            }

            const newDate = new Date();
            const newTime = newDate.setHours(result)
            const newLocationDate = new Date(newTime);

            let newHours = newLocationDate.getHours();

            if(newHours >= 0 && newHours < 10){
                newHours = `0${newHours}`
            }

            return `${newHours}:${dateObject.minute}`
        }
    }

    return(
        <div className={Style.TempForecast}>
            <h6 className={Style.Date}>{calcTime(location.timezone, null)}</h6>
            <div className={Style.ForecastContainer}> 
                {
                    data.list.map(item => {
                        return(
                            <div className={Style.ForecastItem} key={Math.random()}>
                                <h6 className={Style.Time}>{calcTime(location.timezone, item.dt_txt)}</h6>
                                <span aria-label="Weather icon" className={Style.ForecastIcon}><i className={item.weather[0].id === null ? "fas fa-bug" : `wi wi-owm-${item.weather[0].id}`}></i></span>  
                                <h6 className={Style.ForecastTemp}>{Math.round(item.main.temp)}</h6>
                            </div>
                        );
                    })
                }
            </div> 
        </div>
    );
}