import React from 'react';
import {Link} from "react-router-dom";

// Components
import {Figure} from './components/Figure/Figure';
import {TempGroup} from './components/TempGroup/TempGroup';
import {TempForecast} from './components/TempForecast/TempForecast';
import {TempInfo} from './components/TempInfo/TempInfo';

// Style sheets
import Style from './Location.module.scss';
import '../../Default.scss';

export function Location(props){
    let location = props.location === null 
        ? {"weather": [{"id": null,"description": "Please go back to locations",}],"base": "stations","main": {"temp": 0,"feels_like": 0,"pressure": 0,"humidity": 0,"temp_min": 0,"temp_max": 0},"wind": {"speed": 0,"deg": 360},"dt": 0,"sys": {"country": "#",},"id": 2643743,"name": "Error",}
            : props.location;
    let data = props.data === null ? {"list": [{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},{"dt": 0,"main": {"temp": 0,},"weather": [{"id": null,}],},]} : props.data;
    const tempType = props.tempType;

    function calcColor(temp, tempType){
        let tempColor = null;

        if(tempType === "metric"){
            tempColor = (temp >= 36) 
                ? "Hot" : (temp >= 30) 
                    ? "Warmest" : (temp >= 25) 
                        ? "Warmer" : (temp >= 20) 
                            ? "Warm" : (temp >= 15) 
                                ? "Cold" : (temp >= 10) 
                                    ? "Colder" : (temp >= 0) 
                                        ? "Coldest" : "IceCold"
        } else if(tempType === "imperial"){  
            tempColor = (temp >= 96.8) 
                ? "Hot" : (temp >= 86) 
                    ? "Warmest" : (temp >= 77) 
                        ? "Warmer" : (temp >= 68) 
                            ? "Warm" : (temp >= 59) 
                                ? "Cold" : (temp >= 50) 
                                    ? "Colder" : (temp >= 32) 
                                        ? "Coldest" : "IceCold"
        }

        return `${Style.Container} ${tempColor}`;
    }

    return(
        <React.Fragment>            
            <header className={Style.Header}>
                <Link aria-label="View locations" to="/"><button className={Style.Button} onClick={props.onClick}><i className="fas fa-stream"></i></button></Link>
                <div className={Style.Right}>
                    <Link aria-label="View locations" to="/"><button aria-label={`Delete ${location.name}`} onClick={() => props.onClickDelete(location.id, location.name)} className={`${Style.Button} ${Style.Trash}`}><i className="fas fa-trash"></i></button></Link>
                    <button aria-label={tempType === "metric" ? "Change units to fahrenheit" : "Change units to celcius"} className={Style.Button} onClick={props.handleClickTempType}>
                        <span className={tempType === "metric" ? Style.Active : Style.Inactive}><span>&#176;</span>C</span> / <span className={tempType === "metric" ? Style.Inactive : Style.Active}><span>&#176;</span>F</span></button>
                </div>
            </header> 
            <React.Fragment>
                <section className={calcColor(location.main.temp, tempType)}>
                    <h1 className={Style.Title}>{location.name} <span className={Style.Country}>({location.sys.country})</span></h1>
                    <Figure location={location} />
                    <TempGroup location={location} />
                    <TempForecast location={location} data={data} />
                    <TempInfo location={location} tempType={tempType} />
                </section>
            </React.Fragment>
        </React.Fragment>
    );
}